body {
  justify-content: center;
  margin: 0;
  padding: 0;
  font-family: Roboto, sans-serif;
  display: flex;
}

.product-card {
  box-sizing: border-box;
  background-color: #fff;
  border: 1px solid #f3f3f3;
  border-radius: 5px;
  flex-direction: column;
  align-items: center;
  width: 200px;
  padding: 16px;
  display: flex;
}

.product-card__image {
  width: 160px;
  height: 134px;
  margin-top: 16px;
}

.product-card__name {
  color: #060b35;
  width: 166px;
  margin-top: 40px;
  font-size: 12px;
  font-weight: 500;
  line-height: 18px;
  text-decoration: none;
}

.product-card__code {
  color: #616070;
  width: 166px;
  margin: 4px 0 0;
  font-size: 10px;
  font-weight: 400;
  line-height: 14px;
}

.product-card__price {
  justify-content: space-between;
  width: 166px;
  margin-top: 24px;
  display: flex;
}

.product-card__price_label {
  color: #616070;
  font-size: 12px;
  font-weight: 400;
  line-height: 18px;
}

.product-card__price_value {
  color: #060b35;
  text-align: right;
  font-size: 16px;
  font-weight: 700;
  line-height: 18px;
}

.product-card__buy {
  color: #fff;
  text-align: center;
  background-color: #00acdc;
  border-radius: 5px;
  width: 166px;
  height: 40px;
  margin-top: 16px;
  font-size: 14px;
  font-weight: 700;
  line-height: 40px;
  text-decoration: none;
}

.product-card__buy:hover {
  color: #00acdc;
  background-color: #fff;
  border: 1px solid #00acdc;
}

.product-card__reviews {
  justify-content: space-between;
  align-items: end;
  width: 166px;
  margin-top: 16px;
  display: flex;
}

.product-card__reviews_label {
  color: #060b35;
  font-size: 10px;
  font-weight: 400;
  line-height: 14px;
}

.stars {
  justify-content: center;
  display: flex;
}

.stars__star {
  background-image: url("star.822d561e.svg");
  background-position: center;
  background-repeat: no-repeat;
  width: 16px;
  height: 16px;
  margin-right: 4px;
}

.stars--1 :nth-child(-n+1) {
  background-image: url("star-active.edda4c43.svg") !important;
}

.stars--2 :nth-child(-n+2), .stars--3 :nth-child(-n+3), .stars--4 :nth-child(-n+4), .stars--5 :nth-child(-n+5) {
  background-image: url("star-active.edda4c43.svg");
}
